import React, { useState, useEffect, useCallback } from 'react';
import { App , Spin} from 'antd';
import { useTranslation } from 'react-i18next';
import {useStore} from '../../utils/store'
import copy from 'copy-to-clipboard';
import {request} from '../../api/request'

function Invite() {
    
    const {t, i18n} = useTranslation()
    const {message} = App.useApp()
    const [state, dispatch] = useStore();
    
    
    const handleClick = (val:string) =>{
        copy(val)
        message.success(`${t("Yescopy")}`)
    }
    
    
    const IcodeInfo = () => {
        if (!state.Icode) {
            return (<Spin />)
        } else {
            return (
                <>{state.Icode}</>
           )
        }

    }
 
    useEffect(() => {
        IcodeInfo()
    }, [state.Icode]);
            

    
    return (
        <div className="db-table-main">
            {state.userName ? (<><div className="db-table-info">
                <span>{t("Icode")}</span>
            </div>
               <div className="db-table-info">
                   <div onClick={()=>handleClick(state.Icode)}><IcodeInfo /><span className="tocopy">{t("clicktocopy")}</span></div>
               </div></>) : (<></>)}
        </div>
    )
}

export default Invite;